.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: var(--dark-color);
  border: 1px solid var(--dark-color);
  border-radius: 16.172px;
  padding: 15.17px 23.26px;
  color: #FFF;
  fill: var(--light-color);
  stroke: var(--light-color);
  font-family: var(--font-inter);
  font-size: 16.172px;
  font-weight: 600;
  line-height: 150%;
  text-transform: uppercase;
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out, color 0.3s ease-in-out, fill 0.3s ease-in-out, color 0.3s ease-in-out, stroke 0.3s ease-in-out, color 0.3s ease-in-out;

  &:focus {
    outline: none;
  }

  @include hover {
    border-color: rgba(28, 29, 32, 0.08);
    color: var(--light-color);
    fill: var(--light-color);
    stroke: var(--light-color);
    background-color: var(--accent-color);
    box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
  }

  &:active {
    border-color: var(--active-color);
    color: var(--active-color);
    fill: var(--active-color);
    stroke: var(--active-color);
    background-color: var(--light-color);
  }
}

.btn.focus-visible {
  border-color: var(--active-color);
  color: var(--active-color);
  background-color: var(--light-color);
  fill: var(--active-color);
  stroke: var(--active-color);
}

.btn-explore {
  align-self: end;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
	text-decoration: none;

  @include mobile {
    align-self: center;
  }


  &::before {
    content: '';
    position: absolute;
    left: 0; bottom: -2px;
    width: 0%;
    height: 1.5px;
    background-color: #ACADB9;
    transition: width 0.35s, background-color 0.3s ease-in-out;
  }

  span {
    color: #ACADB9;
    font-family: var(--font-poppins);
    font-size: 17.67px;
    font-style: normal;
    font-weight: 500;
    line-height: 39.92px; /* 225.921% */
    letter-spacing: -0.088px;
    margin-right: 16px;
  }

  img {
    width: 14px;
    height: 14px;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    &::before {
      width: 100%;
    }
  }

  &:active {
    &::before {
      background-color: var(--dark-color);
    }
  }
}

.btn-explore.focus-visible {
  outline: 1px solid var(--active-color);
  border-radius: 15%;
}
