.sell {
  padding-top: 55px;
  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__form {
  }

  &__title {
    margin: 0;
    margin-bottom: 153px;
    color: #141416;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-style: normal;
    font-weight: 600;
    line-height: 66.676%;
    text-align: center;

    @include mobile {
      font-size: 35px;
    }
  }

  &__bottom {
    align-self: start;
    width: 100%;

    @include medium-tablet {
      align-self: inherit;
    }
  }
}

.form-sell {
  padding-bottom: 210px;

  @include tablet {
    grid-template-columns: repeat(12, 1fr);
    column-gap: 44px;
  }

  @include mini-mobile {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 42px;
  }

  @include medium-tablet {
    grid-template-columns: repeat(4, 1fr);
  }


  &__left {
    flex-direction: column;
    grid-column: 7 span;


    @include small-notebook {
      grid-column: 6 span;
    }

    @include tablet {
      grid-column: 6 span;
    }

    @include medium-tablet {
      grid-column: 4 span;
      order: 2;
    }
  }

  &__right {
    grid-column: 5 span;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;

    @include small-notebook {
      grid-column: 8 / 12;
    }

    @include tablet {
      grid-column: 8 / 12;
      padding-right: 10px;
    }

    @include medium-tablet {
      grid-column: 4 span;
      order: 1;
      margin-bottom: var(--offset);
    }
  }

  &__image {
    width: 468px;
    height: 413px;
    border-radius: 30.744px;
    background: var(--neutrals-8, #FCFCFD);
    box-shadow: 0px 81.98480987548828px 81.98480987548828px -61.48860168457031px rgba(15, 15, 15, 0.10), 0px -81.9800033569336px 80px 0px rgba(0, 0, 0, 0.05);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 57px;

    img {
      max-height: 100%;
      flex-shrink: 0;
    }

    @include mobile {
      max-width: 468px;
      width: 100%;
      height: 413px;
      max-height: 413px;

      img {

      }
    }
  }

  &__label  {
    flex-direction: column;
    margin-bottom: 35px;
    width: 100%;

    &:last-child {
      margin: 0;
    }

    span {
      margin-bottom: 20px;
      color: #000;
      font-family: var(--font-poppins);
      font-size: 20px;
      font-weight: 500;
      line-height: 124.5%;
    }

    &-right {
      width: 40%;
    }
  }

  &__buttons {
    position: relative;
  }

  &__container-top {
    width: 50%;
    column-gap: 35px;

    @include medium-tablet {
      width: 100%;
    }
  }

  &__container-bottom {
    width: 100%;
    column-gap: 35px;
  }

  &__input-file {
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
  }

  &__label-file {
    width: 80%;
  }

  &__input {
    border-radius: 11.526px;
    background: #EFEFEF;
    width: 100%;
    height: 62.5px;
    padding: 29px 10px 29px 26px;

    &::placeholder {
      color: #9596A6;
      font-family: var(--font-poppins);
      font-size: 13.447px;
      font-weight: 400;
      line-height: 124.5%;
    }


    &-big {
      height: 165px;

      &::placeholder {
      }
    }

    &-left {
      padding-left: 119px;
    }
  }

  &__link {
    position: absolute;
    top: 18px;
    right: 20px;
  }

  &__sel {
    position: relative;

    .choices[data-type*=select-one]:after {
      transition: transform 0.3s ease-in-out, margin-top 0.3s ease-in-out;
    }

    .choices[data-type*=select-one].is-open:after {
      margin-top: -8px;
      border: none;
      transform: rotate(180deg);
    }

    .choices[data-type*=select-one] .choices__inner {
      padding: 0px 0px 0px 21px;
    }


    .choices.focus-visible {
      border: 1px solid #000;
      border-radius: 12.332px;
    }


    .choices {
      width: 100%;
      height: 62.5px;

      &::after {
        border: none;
        background: url('../img/sell/choice.svg') 0 -2px no-repeat;
        background-size: 10px;
        width: 10px;
        height: 10px;
        right: 23px;
        top: 50%;
      }

      &__list {

        &--single {
          padding: 0;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      &__inner {
        height: 100%;
        border-radius: 12.332px;
        background: #EFEFEF;
        border: none;
        color: #9596A6;
        font-family: var(--font-poppins);
        font-size: 13.447px;
        font-weight: 400;
        letter-spacing: -0.269px;
      }
    }

    &-first {
    position: relative;
    border-radius: 11.526px;

    .choices[data-type*=select-one]:after {
      transition: transform 0.3s ease-in-out, margin-top 0.3s ease-in-out;
    }

    .choices[data-type*=select-one].is-open:after {
      margin-top: -8px;
      border: none;
      transform: rotate(180deg);
    }

    .choices.focus-visible {
      border: 1px solid #000;
      border-radius: 11.526px;
    }

    .choices {
      position: absolute;
      top: 5%;
      left: 1%;
      height: 95%;


      &::after {
        border: none;
        background: url('../img/sell/choice.svg') 0 -2px no-repeat;
        background-size: 10px;
        width: 10px;
        height: 10px;
        right: 23px;
        top: 50%;
      }

      &__list {

        &--dropdown {
          word-break: normal;
        }

        &--single {
          padding: 0;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      &__inner {
        position: relative;
        top: 17.5%;
        left: 0;
        height: 75%;
        color: #9596A6;
        font-family: var(--font-poppins);
        font-size: 13.447px;
        font-weight: 400;
        letter-spacing: -0.269px;
        border: none;
        padding: 0px 46px 0px 27px;
        background: #EFEFEF;
        padding-bottom: 0;
        border-right: solid 1px #dadada;
      }
    }
  }
}

  &__btn {
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: 115.314%;
    text-transform: capitalize;
    border-radius: 11.993px;
  }

  &__label-checkbox {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0;
    margin-bottom: 40px;
  }

  &__label-last {
    margin-bottom: 110px;
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    color: #000;
    font-family: var(--font-poppins);
    font-size: 20px;
    font-weight: 500;
    line-height: 124.5%;
  }

  &__descr {
    margin: 0;
    color: #9596A6;
    font-family: var(--font-poppins);
    font-size: 13.447px;
    font-weight: 400;
    line-height: 124.5%;
  }

  &__btns {
    position: relative;
    display: inline-block;
    width: 55px;
    height: 30px;
  }

  &__checkbox {
    appearance: none;
  }

  &__checkbox:checked + &__slider {
    background-color: #000;
  }

  &__checkbox:checked + &__slider::before {
    transform: translateX(26px);
    background-color: var(--light-color);
  }

  &__slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #E7E4E4;
    height: 100%;
    -webkit-transition: .4s;
    transition: .4s;
    border-radius: 34px;

    &::before {
      position: absolute;
      content: "";
      height: 20px;
      width: 20px;
      left: 4px;
      bottom: 5px;
      background-color: #000;
      -webkit-transition: .4s;
      transition: .4s;
      border-radius: 50%;
    }
  }

  &__span-btn {
    width: 100%;
    color: var(--white, #FFF);
    font-family: var(--font-poppins);
    font-size: 15px;
    font-weight: 500;
    line-height: 153.333%;
    letter-spacing: 0.45px;
    text-transform: capitalize;
    cursor: pointer;
  }
}

.focus-visible + .btn {
  border-color: var(--active-color);
  color: var(--active-color);
  background-color: var(--light-color);
}

.focus-visible + .form-sell__slider {
  box-shadow: 0 0 1px #000;
}
