.graph-modal {
  background-color: initial;
  backdrop-filter: blur(10.733402252197266px);

  &__container {
    max-width: 438.9px;
    border: 1.508px solid #000;
    padding: 44px 19px 40px 19px;

    @include small-tablet {
      padding: 30px 13px;
      max-width: 312px;
    }
  }
}

.content-modal {
  &__header {
    margin-bottom: 13px;
  }

  &__title {
    margin: 0;
    margin-bottom: 3px;
    color: #000;
    font-family: var(--font-poppins);
    font-weight: 600;
    line-height: 144.444%;
    font-size: 20.257px;

    @include small-tablet {
      font-size: 14.406px;
    }
  }

  &__date {
    margin: 0;
    color: #888;
    font-family: var(--font-poppins);
    font-size: 14.63px;
    font-weight: 400;
    line-height: 123.077%;

    &-color {
      color: #9c9c9c;
    }

    @include small-tablet {
      font-size: 10.405px;
    }
  }

  &__list {
    margin-bottom: 30px;
  }

  &__item {
    margin-bottom: 13px;
  }

  &__link {
    align-items: center;
    justify-content: space-between;
    padding: 18px 50px 18px 18px;
    background-color: var(--light-color);
    box-shadow: 0px 3.2014122009277344px 38.41693878173828px 0px rgba(197, 197, 197, 0.25);
    border-radius: 8.004px;
    transition: box-shadow 0.3s ease-in-out;

    &:last-child {
      margin: 0;
    }

    @include small-tablet {
      padding: 13px 30px 13px 12px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      box-shadow: -4.16183614730835px 8.3236722946167px 14.406353950500488px 0px rgba(16, 16, 16, 0.15);
    }

    &:active {
      box-shadow: -10.16183614730835px 10.3236722946167px 16.406353950500488px -4px rgba(16, 16, 16, 0.15);
    }
  }

  &__left {
    align-items: center;
  }

  &__image {
    position: relative;
    margin-right: 11px;

    &-avatar {
      width: 63.671px;
      height: 63.671px;

      @include small-tablet {
        width: 45px;
        height: 45px;
      }
    }

    &-confirm {
      width: 20px;
      height: 20px;
      position: absolute;
      top: 0;
      right: 0;

      @include small-tablet {
        width: 14.771px;
        height: 14.771px;
      }
    }
  }

  &__name {
    color: #000;
    font-family: var(--font-poppins);
    font-weight: 500;
    line-height: 153.333%;
    font-size: 16.881px;

    @include small-tablet {
      font-size: 12.005px;
    }
  }

  &__bid {
    color: #000;
    font-family: var(--font-poppins);
    font-size: 20.234px;
    font-weight: 500;
    padding-left: 22px;
    background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
    background-size: 21px;


    @include small-tablet {
      padding-left: 15px;
      background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
      background-size: 15px;
      font-size: 14.39px;
    }
  }

  &__footer {

    &-title {
      margin: 0;
      margin-bottom: 12px;
      color: #000;
      font-family: var(--font-poppins);
      font-size: 20.257px;
      font-weight: 600;
      line-height: 144.444%;
    }
}

  &__form {
    flex-direction: column;
  }

  &__label {
    position: relative;
    margin-bottom: 13px;
  }


  &__select {
    position: relative;
    z-index: 2;
    border: none;
    padding: 25px 16px;
    border-radius: 18.006px 0px 0px 18.006px;
    background: var(--dark-color);
    color: #FFF;
    font-family: var(--font-poppins);
    font-size: 20.257px;
    font-weight: 600;
    appearance: none;
    cursor: pointer;
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: rgba(28, 29, 32, 0.08);
      color: var(--light-color);
      background-color: var(--accent-color);
    }

    &:active {
      border-color: rgba(28, 29, 32, 0.08);
      color: var(--light-color);
      background-color: rgba(28, 29, 32, 0.514);
    }
  }


  &__input {
    border-radius: 18.006px;
    border: 1.125px solid #141416;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    padding-left: 100px;
    color: var(--light-100, #000);
    font-family: var(--font-poppins);
    font-size: 16.881px;
    font-weight: 500;
    line-height: 153.333%;

    &::placeholder {
      visibility: hidden;
    }
  }

  &__btn {
    text-transform: none;
    border-radius: 18.006px;
    padding: 23px 0;
    font-size: 16.881px;
    font-style: normal;
    font-weight: 600;
    line-height: 153.333%;

    img {
      margin-right: 12px;
    }

    &:active {
      border-color: rgba(28, 29, 32, 0.08);
      color: var(--light-color);
      background-color: rgba(28, 29, 32, 0.514);
    }
  }
}

.content-modal__link.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.content-modal__btn.focus-visible {
  border-color: rgba(28, 29, 32, 0.08);
  color: var(--light-color);
  background-color: rgba(28, 29, 32, 0.514);
}

.content-modal__select.focus-visible  {
  outline: 2px solid #9c9c9c;
  box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
}

