.collection {
  position: relative;
  padding-top: 236px;
  padding-bottom: 100px;

  @include tablet {
    padding-top: 168px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-bottom: 100px;
  }

  &__container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 100px;
    font-family: var(--font-poppins);
    color: var(--dark-color);
    font-size: 45.04px;
    font-weight: 600;
    line-height: 99.357%;

    @include tablet {
      font-size: 32.032px;
      margin-bottom: 64px;
    }

    @include mobile {
      margin-bottom: 40px;
      font-size: 30px;
      line-height: 44.751px;
    }
  }

  &__button {
    margin-top: 85px;

    @include tablet {
      margin-top: 60px;
    }

    @include mobile {
      margin-top: 41px;
    }
  }
}

.table-collection {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 10px 30px;


  th {
    padding-bottom: 8px;
    text-align: start;
  }

  td {
    border-bottom: 1.69px solid #ebe9e9;
    padding-bottom: 24px;
    padding-top: 40px;
  }


  &__header {
    margin: 0;

    .table-collection__string-header {
      color: #93989A;
      font-family: var(--font-poppins);
      font-size: 22px;
      font-weight: 500;
      line-height: 130%; /* 28.6px */
      letter-spacing: 0.22px;

      @include tablet {
        font-size: 15.646px;
        letter-spacing: 0.156px;
      }
    }

    &-volume {

    }

    &-price {
      padding-left: 10px;
      @include small-tablet {
        display: none;
      }
    }

    &-owners {
      padding-left: 10px;
      @include small-tablet {
        display: none;
      }
    }

    &-items {
      @include small-tablet {
        display: none;
      }
    }

    &-24h {
      @include mobile {
        display: none;
      }
    }
  }

  &__string {
    font-family: var(--font-poppins);
  }

  &__name {

  }

  &__image {
    margin-right: 25px;

    @include tablet {
      margin-right: 17px;
    }

    img {
      border-radius: 1750.911px;
      max-width: 80px;
      height: 80px;

      @include tablet {
        max-width: 60px;
        height: 60px;
      }
    }
  }

  &__label {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 28.015px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;

    @include tablet {
      font-size: 19.923px;
    }
  }

  &__author {
    color: #64748B;
    font-family: var(--font-poppins);
    font-size: 22.762px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    @include tablet {
      font-size: 16.188px;
    }
  }

  &__volume {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    padding-left: 34.5px;
    font-size: 28.015px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
    background-size: 31px;

    @include tablet {
      font-size: 17.433px;
      padding-left: 24.5px;
      background-size: 21px;
    }

    @include mobile {
      margin-bottom: 8px;
    }
  }

  &__24h {
    color: #10C352;
    font-family: var(--font-poppins);
    font-size: 24.513px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 31.867px */
    letter-spacing: 0.245px;

    @include tablet {
      font-size: 17.433px;
    }

    @include mobile {
      display: none;
    }

    &-volume {
      color: #10C352;
      font-family: var(--font-poppins);
      font-size: 24.513px;
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 31.867px */
      letter-spacing: 0.245px;
      display: none;

      @include tablet {
        font-size: 17.433px;
      }

      @include mobile {
        display: block;
      }
    }
  }

  &__price {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 24.513px;
    padding-left: 34.5px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    background: url('../img/weekly-top/ethereum.svg') left 65px no-repeat;
    background-size: 31px;

    @include tablet {
      font-size: 17.433px;
      padding-left: 24.5px;
      background-size: 21px;
      background-position-y: 61px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__owners {
    color: #010101;
    font-family: var(--font-poppins);
    font-size: 23.654px;
    font-weight: 500;
    line-height: 130%; /* 30.75px */
    letter-spacing: 0.237px;

    @include tablet {
      font-size: 16.822px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__items {
    color: var(--black-01, #010101);
    font-family: var(--font-poppins);
    font-size: 23.654px;
    font-weight: 500;
    line-height: 130%; /* 30.75px */
    letter-spacing: 0.237px;

    @include tablet {
      font-size: 16.822px;
    }

    @include small-tablet {
      display: none;
    }
  }
}
