.recent {
  position: absolute;
  top: -128px;
  right: 100px;
  z-index: 21;

  @include notebook {
    right: 70px;
  }

  @include medium-tablet {
    display: none;
  }

  &__container {
    width: 365px;
    padding: 26px;
    border-radius: 16px;
    border: 1px solid var(--greyscale-200, #EDF2F7);
    background: var(--others-white, #FFF);
    box-shadow: 10px 10px 50px 0px rgba(20, 20, 22, 0.10);

    @include notebook {
      width: 259px;
    }
  }

  &__top {
    align-items: center;
    justify-content: space-between;
    margin-bottom: 34px;
  }

  &__title {
    margin: 0;
    color: var(--greyscale-900, #1A202C);
    font-family: var(--font-poppins);
    font-size: 21px;
    font-weight: 600;
    line-height: 135%; /* 28.35px */
    letter-spacing: 0.2px;

    @include notebook {
      font-size: 16.935px;
      letter-spacing: 0.142px;
    }
  }

  &__button {
    transition: transform 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      transform: rotate(90deg);
    }
  }
}

.list-recent {
  counter-reset: number 0;

  &__item {
    align-items: center;
    justify-content: space-between;
    counter-increment: number;
    margin-bottom: 33px;

    &:last-child {
      margin: 0;
    }
  }

  &__left {

    &:focus {
      outline: none;
    }
  }

  &__image {
    position: relative;
    margin-right: 21px;

    @include notebook {
      margin-right: 15px;
    }

    img {
      border-radius: 960.245px;

      @include notebook {
        width: 33px;
      }
    }

    &:after {
      content: counter(number);
      color: var(--dark-color);
      width: 21px;
      height: 21px;
      position: absolute;
      bottom: 0;
      right: -2px;
      border-radius: 50%;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      font-family: var(--font-poppins);
      font-size: 11.523px;
      font-weight: 700;
      line-height: 150%; /* 17.284px */
      letter-spacing: 0.096px;
      background-color: #fff;

      @include notebook {
        font-size: 8.195px;
        letter-spacing: 0.068px;
      }
  }
  }

  &__text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__name {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 15.364px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;

    @include notebook {
      font-size: 10.927px;
    }
  }

  &__nick {
    color: #64748B;
    font-family: var(--font-poppins);
    font-size: 12.483px;
    font-weight: 400;
    line-height: 150%;

    @include notebook {
      font-size: 8.878px;
    }
  }

  &__right {
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
  }

  &__eth {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 15.364px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    padding-left: 23px;
    background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
    background-size: 21px;
    margin-bottom: 7px;
    position: relative;
    text-decoration: none;
    transition: color 0.3s ease-in-out;

    @include notebook {
      background-size: 13px;
      font-size: 10.927px;
      padding-left: 15px;
    }


    &::before {
      content: '';
      position: absolute;
      right: -5px; top: 0px;
      height: 0%;
      width: 1.8px;
      background-color: var(--dark-color);
      transition: height 0.35s, background-color 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      &::before {
        height: 100%;
      }
    }

    &:active {
      color: #676767;
      &::before {
        background-color: #676767;
      }
    }
  }

  &__24h {
    font-family: var(--font-poppins);
    font-size: 13.443px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    @include notebook {
      font-size: 9.561px;
    }

    &-minus {
      color: #E23333;
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: #9c2525;
      }

      &:active {
        color: var(--active-color);
      }
    }

    &-plus {
      color: #10C352;
      transition: color 0.3s ease-in-out;

      &:focus {
        outline: none;
      }

      @include hover {
        color: #097430;
      }

      &:active {
        color: var(--active-color);
      }
    }
  }
}

.recent__button.focus-visible {
  border: 1.5px solid var(--active-color);
  border-radius: 25%;
}

.list-recent__eth.focus-visible {
  border: 1.5px solid var(--active-color);
  padding-right: 1.5px;
  border-radius: 10%;
}

.list-recent__24h.focus-visible {
  outline: 1.5px solid var(--active-color);
  border-radius: 10%;
  padding: 1.5px;
}

.list-recent__left.focus-visible {
  outline: 1.5px solid var(--active-color);
  border-radius: 10%;
}
