.form {
  width: 100%;
  max-width: 348px;

  @include tablet {
    max-width: 247px;
    min-width: 207.47px;
  }

  @include small-tablet {
    max-width: 195px;
    min-width: 0;
  }
}

.form__input {
  padding: 20px 0;
  width: 100%;
  border-radius: 20px;
  padding-left: 70px;
  background: url("../img/header/search.svg");
  background-position: left 23px center;
  background-size: 24px 24px;
  background-repeat: no-repeat;
  transition: color 0.3s ease-in-out, border-color 0.3s ease-in-out;
  background-color: #EDEDED;

  @include tablet {
    padding: 14px 0;
    padding-left: 50px;
    background-position: left 14px center;
    background-size: 15px;
  }

  &::placeholder {
    color: #C2C3CB;
    font-family: var(--font-poppins);
    font-size: 15.711px;
    font-weight: 400;
    line-height: 117.5%;
    letter-spacing: -0.314px;

    @include tablet {
      font-size: 11.174px;
    }
  }
}
