.profile {
  padding-bottom: 100px;

  &__container {
  }

  &__top {
  }

  &__bottom {
    column-gap: 132px;

    @include medium-tablet {
      flex-direction: column;
      column-gap: 0;
    }
  }

  &__background {

    img {
      border-radius: 22px;
      box-shadow: 15.264140129089355px 15.264140129089355px 53.07101821899414px 0px rgba(0, 0, 0, 0.12);
    }
  }

  &__left {
    flex: 0 1 39%;
    padding-left: 71px;

    @include medium-tablet {
      padding: 0 10px;
    }
  }

  &__right {
    flex: 0 1 61%;
    padding-top: 73px;
  }
}

.card-profile {


  &__avatar {
    position: relative;
    top: -70px;
    left: 0px;
    width: fit-content;

    &-person {

    }

    &-confirmed {
      position: absolute;
      bottom: 7px;
      right: 18px;
    }
  }

  &__main {
    justify-content: space-between;
    padding-bottom: 40px;
    border-bottom: 1.06px solid #d0d0d0;
    margin-bottom: 43px;
  }

  &__title {
    margin: 0;
    margin-bottom: 4px;
    color: var(--black-01, #010101);
    font-family: var(--font-outfit);
    font-size: 35px;
    font-weight: 600;
    line-height: 130%;
    letter-spacing: 0.35px;
  }

  &__subtitle {
    margin: 0;
    color: var(--grey-01, #93989A);
    font-family: var(--font-outfit);
    font-size: 18px;
    line-height: 130%; /* 23.4px */
    letter-spacing: 0.18px;
  }

  &__button {
    align-self: flex-start;
    padding: 12px 23px;
    letter-spacing: 0.148px;
    font-weight: 500;
    font-size: 14px;
  }

  &__numbers {
    margin: 0;
    justify-content: space-between;
    margin-bottom: 116px;
  }

  .numbers__item {
    margin-right: 0;
  }

  &__label {
    color: #C5C5C5;
    font-family: var(--font-public);
    font-size: 25px;
    font-weight: 700;
    line-height: 160.772%;
  }

  &__descr {
    margin: 0;
    padding-bottom: 85px;
    margin-bottom: 33px;
    color: #949494;
    font-family: var(--font-poppins);
    font-size: 14px;
    font-weight: 400;
    line-height: 177%;
    text-transform: capitalize;
    border-bottom: 1.06px solid #d0d0d0;
  }

  &__list-social {
    display: flex;
  }

  &__item-social {
    margin-right: 66px;

    &:last-child {
      margin: 0;
    }

    @include mobile {
      margin-right: 30px;
    }
  }

  &__link-social {
    svg {
      width: 16px;
      height: 16px;
      fill: #000;
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        fill: #9b9b9b;
      }
    }

    &:active {
      svg {
        fill: var(--accent-color);
      }
    }
  }
}

.tab {

  &__nav {
    justify-content: center;
    margin-bottom: 95px;
  }

  &__nav-item {
    width: 50%;
    display: flex;
    justify-content: center;
  }

  &__nav-btn {
    padding: 18px 0;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    text-transform: none;
    cursor: pointer;
    width: 100%;

    span {
      color: var(--light-50, #D2D2D2);
      font-family: var(--font-poppins);
      font-size: 16.247px;
      font-weight: 700;
      line-height: 153.333%;
      transition: color 0.3s ease-in-out;
    }

    svg {
      margin-right: 6px;
      path {
        fill: #D2D2D2;
        transition: fill 0.3s ease-in-out;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      span {
        color: #a5a5a5;
      }

      svg {
        path {
          fill: #a5a5a5;
        }
      }
    }

    &-activity {
    }

    &-collection {

    }
  }

  &__nav-btn--active {
    border-bottom: 3.249px solid var(--dark-color);

    span {
      color: #000;
    }

    svg {
      path {
        fill: #130F26;
      }
    }

    @include hover {
      span {
        color: #000;
      }

      svg {
        path {
          fill: #000000;
        }
      }
    }
  }

  &__panel {
    transition: opacity 5s ease-in-out;
    display: none;
    opacity: 0;
  }

  &__panel--active {
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 100%;
    transition: opacity 5s ease-in-out;
  }

  &__list {
    width: 100%;
    column-gap: 30px;
    row-gap: 50px;
    flex-wrap: wrap;

    @include notebook {
      justify-content: center;
    }
  }

  &__item {
    min-width: initial;
    max-width: initial;
    flex-direction: column;
    justify-content: space-between;
    color: inherit;
    padding: 10px 14px 14px;
    background-color: var(--light-color);
    border-radius: 23.479px;
    max-width: 204.358px;
  }

  &__card {
    width: 100%;


    @include mini-mobile {

    }
  }

  &__top {
    position: relative;
    margin-bottom: 10px;
  }

  &__image {

    img {
      border-radius: 23px;
    }
    margin-bottom: 13px;
  }

  &__timer {
    position: absolute;
    right: 14px;
    top: 11px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 8px 14px;
    border: 1.176px solid rgba(28, 29, 32, 0.08);
    border-radius: 9.41px;
    background-color: rgba(28, 29, 32, 0.35);
    box-shadow: 0px 4.705041408538818px 14.115126609802246px 0px rgba(28, 29, 32, 0.08);
    backdrop-filter: blur(2.352520704269409px);
    color: var(--light-color, #FFF);
    font-family: var(--font-poppins);
    font-size: 14.115px;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: 0.118px;
    margin-bottom: 13px;
  }

  &__title {
    margin: 0;
    color: #141416;
    font-family: var(--font-poppins);
    font-size: 15.391px;
    font-weight: 600;
    line-height: 140%; /* 21.547px */
  }

  &__bottom {
    align-items: center;
    justify-content: space-between;

    @include notebook {
      align-items: initial;
      flex-direction: column;
    }

    @include tablet {
      align-items: center;
      flex-direction: row;
    }

    @include mobile {
      align-items: initial;
      flex-direction: column;
    }
  }

  &__left {
    display: inline-flex;
    flex-direction: column;

    @include notebook {
      margin-bottom: 20px;
    }

    @include tablet {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__bid {
    color: #94A3B8;
    font-family: var(--font-poppins);
    font-size: 10.261px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%; /* 15.391px */
    letter-spacing: 0.086px;
  }

  &__amount {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 11.971px;
    font-weight: 500;
    line-height: 150%;
    padding-left: 17px;
    background: url('../img/weekly-top/ethereum.svg') 0 0 no-repeat;
    background-size: 15px;
  }

  &__button {
    text-align: center;
    padding: 11px 18px;
    color: var(--light-color);
    font-family: var(--font-poppins);
    font-size: 10.261px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%; /* 15.391px */
    letter-spacing: 0.086px;
  }

  &__btn {
    cursor: pointer;
  }

  &__buttons {
    width: 100%;
    justify-content: space-between;
    padding-bottom: 59px;
  }

  &__buttn {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 10.016px;
    background-color: var(--light-color);
    box-shadow: 19.379261016845703px 12.919507026672363px 66.19340515136719px 0px rgba(97, 97, 97, 0.25);
    padding: 15px 0;
    margin-right: 18px;
    transition: background-color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      background-color: #f3f3f36e;
    }

    &:active {
      background-color: #e6e6e66e;
    }

    &:first-child {
      width: 50%;
    }

    &:last-child {
      margin-right: 0;
    }

    svg {

      &:first-child {
        margin-right: 13px;
      }
    }

    span {
      margin-right: 13px;
    }
  }

  &__catalogue {
    width: 100%;
    flex-direction: column;
  }

  &__piece {
    width: 100%;
    padding: 15px 20px 20px 15px;
    border-radius: 12.134px;
    background: var(--white, #FFF);
    box-shadow: 18.782058715820312px 12.52137279510498px 64.1535415649414px 0px rgba(97, 97, 97, 0.25);
    margin-bottom: 42px;

    &:last-child {
      margin: 0;
    }
  }

  &__name {
    justify-content: space-between;
    padding-bottom: 15px;
    margin-bottom: 18px;
    border-bottom: 0.607px solid #d2d2d2;
  }

  &__corner {
    align-items: center;


    &:first-child {
      padding-right: 30px;
    }
  }

  &__picture {
    margin-right: 12px;
    flex-shrink: 0;
  }

  &__label {
    color: var(--light-100, #000);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 16.988px;
    font-weight: 600;
    line-height: 157.143%;
  }

  &__sublabel {
    align-items: center;
    color: var(--light-100, #000);
    font-family: var(--font-poppins);
    font-size: 12.08px;
    font-weight: 400;

    span {
      margin-right: 5px;
    }
  }

  &__num {
    height: 80%;
    margin-right: 19px;
    flex-direction: column;
    justify-content: space-between;
  }

  &__sale {
    color: #14A760;
    font-family: var(--font-poppins);
    font-size: 12.08px;
    font-weight: 400;
    text-align: right;
    margin-bottom: 1px;

    &-green {
      color: #14A760;
    }
  }

  &__eth {
    color: var(--light-100, #000);
    text-align: right;
    font-family: var(--font-poppins);
    font-size: 16.037px;
    font-weight: 500;
    margin-bottom: 1px;
  }

  &__time {
    color: var(--light-50, #D2D2D2);
    font-family: var(--font-poppins);
    font-size: 12.08px;
    font-weight: 400;
    text-align: right;
  }

  &__counts {
    justify-content: space-between;
  }

  &__subtitle {
    margin: 0;
    color: var(--gray-3, #828282);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 11.369px;
    font-weight: 400;
    line-height: 123.077%;
  }

  &__count {
    margin: 0;
    color: var(--light-100, #000);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 15.741px;
    font-weight: 500;
    line-height: 144.444%;
  }
}

.card-profile__link-social.focus-visible {
  svg {
    fill: var(--accent-color);
  }
}

.tab__nav-btn.focus-visible {
  span {
    color: #a5a5a5;
  }

  svg {
    path {
      fill: #a5a5a5;
    }
  }
  border-bottom: 3.249px solid #a5a5a5;
}
