.explore-card {
  padding-bottom: 160px;

  &__container {
    flex-direction: column;
  }

  &__top {
    margin-bottom: 132px;
  }

  &__header {
    margin-bottom: 60px;
    align-items: center;
  }
  &__button {
    margin-right: 35px;
  }
  &__title {
    margin: 0;
    color: var(--neutrals-2, #23262F);
    font-family: var(--font-poppins);
    font-size: 31.063px;
    font-weight: 600;
    line-height: 88.889%;
  }
}

.card-explore {
  padding: 37px;
  border-radius: 24.423px;
  background-color: #fafafa;
  box-shadow: 17.560976028442383px 17.560976028442383px 61.05675506591797px 0px rgba(0, 0, 0, 0.12);

  @include small-tablet {
    flex-direction: column;
  }

  &__left {
    flex: 1 1 auto;
    max-width: 663.37px;
    max-height: 629.691px;

    @include small-tablet {
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        border-radius: 13.226px;
      }
    }
  }

  &__right {
    padding-top: 15px;
    flex: 0 1 40%;
    flex-direction: column;
    justify-content: space-between;

    @include notebook {
      padding-left: 15px;
    }

    @include tablet {
      padding: 45px;
    }

    @include medium-tablet {
      padding: 20px;
    }

    @include small-tablet {
      flex: 1 1 auto;
      height: 302px;
    }
  }

  &__text {
    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    color: #292B39;
    font-family: var(--font-poppins);
    font-size: 51.043px;
    font-weight: 600;
    line-height: 62.339%;
    line-height: 100%;

    @include tablet {
      font-size: 36.301px;
      margin-bottom: 15px;
    }

    @include small-tablet {
      font-size: 15.638px;
      margin-bottom: 5px;
    }
  }

  &__descr {
    margin: 0;
    color: rgba(136, 136, 136, 0.70);
    font-family: var(--font-poppins);
    font-size: 19.002px;
    font-weight: 400;
    line-height: 153.333%;

    @include tablet {
      font-size: 13.514px;
    }

    @include small-tablet {
      font-size: 13.031px;
    }
  }

  &__rights {
    justify-content: space-between;

    @include mobile {
      margin-bottom: 10px;
    }
  }

  &__button {

    img {
      margin-right: 16px;
    }

    &:active {
      border-color: rgba(28, 29, 32, 0.08);
      color: var(--light-color);
      background-color: rgba(28, 29, 32, 0.514);
    }
  }
}

.creator-card {
  padding-right: 10px;

  &__image {
    min-height: 67px;
    min-width: 67px;
    margin-right: 17px;

    @include tablet {
      min-height: 46.5px;
      min-width: 46.5px;
      max-height: 46.5px;
      max-width: 46.5px;
    }

    @include small-tablet {
      min-height: 33.43px;
      min-width: 33.43px;
      max-height: 33.43px;
      max-width: 33.43px;
    }
  }

  &__text {
    flex-direction: column;
    justify-content: center;
  }

  &__subtitle {
    color: #8D8D8D;
    font-family: var(--font-poppins);
    font-size: 17.681px;
    font-weight: 400;

    @include tablet {
      font-size: 12.575px;
    }

    @include small-tablet {
      font-size: 9.012px;
    }
  }

  &__title {
    margin: 0;
  }

  &__link {
    color: var(--light-100, #000);
    font-family: var(--font-poppins);
    font-size: 25.568px;
    font-weight: 500;
    line-height: 153.333%;
    transition: color 0.3s ease-in-out;

    @include tablet {
      font-size: 18.183px;
    }

    @include small-tablet {
      font-size: 13.031px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: #000000b4;
    }

    &:active {
      color: #00000056;
    }
  }
}

.owner-card {

  &__image {
    margin-right: 17px;
    min-height: 67px;
    min-width: 67px;

    @include tablet {
      min-height: 46.5px;
      min-width: 46.5px;
      max-height: 46.5px;
      max-width: 46.5px;
    }

    @include small-tablet {
      min-height: 33.43px;
      min-width: 33.43px;
      max-height: 33.43px;
      max-width: 33.43px;
    }
  }

  &__text {
    flex-direction: column;
    justify-content: center;
  }

  &__subtitle {
    color: #8D8D8D;
    font-family: var(--font-poppins);
    font-size: 17.681px;
    font-weight: 400;

    @include tablet {
      font-size: 12.575px;
    }

    @include small-tablet {
      font-size: 9.012px;
    }
  }

  &__title {
    margin: 0;
  }

  &__link {
    color: var(--light-100, #000);
    font-family: var(--font-poppins);
    font-size: 25.568px;
    font-weight: 500;
    line-height: 153.333%;
    transition: color 0.3s ease-in-out;

    @include tablet {
      font-size: 18.183px;
    }

    @include small-tablet {
      font-size: 13.031px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: #000000b4;
    }

    &:active {
      color: #00000056;
    }
  }
}

.bid-card {
  justify-content: space-between;

  @include small-tablet {
    margin-bottom: 10px;
  }

  &__left {
    @include small-tablet {
      padding-right: 10px;
    }
  }

  &__current {
    color: #8C8C8C;
    font-family: var(--font-poppins);
    font-size: 17.861px;
    font-style: normal;
    font-weight: 400;
    line-height: 123.077%;
    text-align: left;
    margin-bottom: 12.5px;

    @include tablet {
      font-size: 12.702px;
    }

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  &__bid {
    color: #292B39;
    font-family: var(--font-poppins);
    font-size: 32.989px;
    font-weight: 600;
    padding-left: 32px;
    background: url('../img/weekly-top/ethereum.svg') 0 0 no-repeat;
    background-size: 35px;
    background-position-x: -5px;

    @include tablet {
      font-size: 23.461px;
      padding-left: 22px;
      background-size: 25px;
    }
  }

  &__end {
    color: #8C8C8C;
    font-family: var(--font-poppins);
    font-size: 17.861px;
    font-style: normal;
    font-weight: 400;
    line-height: 123.077%;
    text-align: right;
    margin-bottom: 17px;

    @include tablet {
      font-size: 12.702px;
    }

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  &__date {
    color: var(--light-100, #000);
    text-align: right;
    font-family: var(--font-poppins);
    font-size: 19.235px;
    font-weight: 400;
    line-height: 157.143%;

    @include tablet {
      font-size: 13.679px;
    }
  }
}

.advanced-cards {
  &__title {
    margin: 0;
    margin-bottom: 44px;
    color: #C3C3C3;
    font-family: var(--font-poppins);
    font-size: 31.063px;
    font-weight: 600;
    line-height: 88.889%;
  }
}

.advanced-card {

  &__list {
    justify-content: center;
  }

  &__item {

    @include small-tablet {
      width: 215px;
    }
  }

  &__wrapper {
    background: #FFF;
    border-radius: 17.452px;
    box-shadow: 17.452444076538086px 11.63496208190918px 59.612003326416016px 0px rgba(97, 97, 97, 0.25);
  }

  &__card {
    flex-direction: column;
    row-gap: 7px;
    padding: 14px;
  }

  &__top {
    img {
      border-radius: 11.635px;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    row-gap: 7px;

    &-top {
      display: flex;
      flex-direction: column;
      row-gap: 7px;
    }

    &-bottom {
      justify-content: space-between;
      align-items: center;
    }
  }

  &__author {
    display: flex;
    align-items: center;
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 14.001px;
    font-weight: 700;
    line-height: 157.143%;
    column-gap: 5px;
    transition: color 0.3s ease-in-out;

    @include tablet {
      font-size: 12.958px;
    }

    &::after {
      content: '';
      display: flex;
      width: 18.223px;
      height: 18.223px;
      background: url('../img/explore/cards/ico.svg') center center no-repeat;
      background-size: 18.223px;

      @include tablet {
        background-size: 15px;
      }
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: rgba(28, 29, 32, 0.562);
    }

    &:active {
      color: rgba(28, 29, 32, 0.288);
    }
  }

  &__title {
    margin: 0;

    &-link {
      color: #141416;
      font-family: var(--font-poppins);
      font-size: 17.387px;
      font-weight: 600;
      line-height: 157.143%;
      transition: color 0.3s ease-in-out;

      @include tablet {
        font-size: 14.365px;
      }

      &:focus {
        outline: none;
      }

      @include hover {
        color: rgba(28, 29, 32, 0.562);
      }

      &:active {
        color: rgba(28, 29, 32, 0.288);
      }
    }
  }

  &__bid {
    color: var(--dark-color);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 18.223px;
    font-weight: 500;
    line-height: 26.322px;
    padding-left: 13px;
    background: url('../img/weekly-top/ethereum.svg') left -5px center no-repeat;
    background-position-y: 3px;
    background-size: 18.223px;

    @include tablet {
      font-size: 12.96px;
      padding-left: 13px;
      background-size: 12px;
      background-position-x: left;
      background-position-y: 6px;
    }
  }

  &__likes {

    &:focus {
      outline: none;
    }

    @include hover {
      path {fill: var(--active-color);}
      span {color: var(--active-color);}
    }

    &:active {
      path {fill: #000;}
      span {color: #000;}
    }

    svg {
      path {
        fill: #888888;
        transition: fill 0.3s ease-in-out;
      }
    }

    span {
      color: var(--70, #888);
      text-align: center;
      font-family: var(--font-poppins);
      font-size: 12.868px;
      font-weight: 600;
      line-height: 144.444%;
      transition: color 0.3s ease-in-out;
    }
  }
}

.creator-card__link.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.owner-card__link.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.card-explore__button.focus-visible {
  border-color: rgba(28, 29, 32, 0.08);
  color: var(--light-color);
  background-color: rgba(28, 29, 32, 0.514);
}

.advanced-card__author.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.advanced-card__title-link.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.advanced-card__likes.focus-visible {
  path {fill: #000;}
  span {color: #000;}
}
