.container--connect {
  padding-left: 48px;
  margin-bottom: 0;
  width: 100%;

  &--footer {
    justify-content: space-between;
    padding-bottom: 47px;
  }
}

.main-connect {
  flex: 1 1 auto;
}

.connect {
  padding-bottom: 110px;
  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__header {
    padding: 45px 0 73px 0;
    flex: 0 1 auto;

    @include tablet {
      padding-top: 32px;
      padding-bottom: 70px;
    }

    @include mobile {
      padding-top: 30px;
      padding-bottom: 0;
      margin-bottom: 0;
    }
  }

  &__footer {
    flex: 0 1 auto;
  }

  &__image {
    flex-shrink: 0;
  }

  &__title {
    margin: 0;
    margin-bottom: 106px;
    color: #323142;
    font-family: var(--font-poppins);
    font-size: 35.25px;
    font-weight: 600;
    line-height: 125%;
    letter-spacing: -1.41px;
    text-align: center;
  }

  &__buttons {
    margin-bottom: 82px;
  }

  &__button {
    width: 395px;
    margin-bottom: 35px;
    padding: 20px 29px;
    padding-right: 0;
    align-items: center;
    border-radius: 9.644px;
    background-color: var(--light-color);
    box-shadow: 5px 10px 50px 0px rgba(0, 0, 0, 0.10);

    &>* {
      margin-right: 15px;

      &:last-child {
        margin: 0;
      }
    }

    span {
      color: var(--light-100, #000);
      font-family: var(--font-poppins);
      font-size: 18.083px;
      font-weight: 600;
      line-height: 153.333%;
    }

    &:last-child {
      margin-bottom: 0;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__next {
    width: 395px;
    padding: 24px 0;
    color: #F3F5F6;
    font-family: var(--font-poppins);
    font-size: 17.942px;
    font-weight: 600;
    letter-spacing: 0.538px;
    text-transform: uppercase;

    @include small-tablet {
      width: inherit;
      max-width: 100%;
    }
  }

  &__foot {
    color: #AAA;
    font-family: var(--font-poppins);
    font-size: 16px;
    font-weight: 500;
    line-height: 150%; /* 24px */
    letter-spacing: 0.2px;

    &--copy {
      text-align: right;
      padding-left: 20px;
    }
  }
}
