.questions {
  padding-bottom: 214px;

  @include tablet {
    padding-bottom: 152px;
  }

  @include small-tablet {
    padding-bottom: 112px;
  }

  &__container {
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  &__title {
    margin: 0;
    margin-bottom: 78.5px;
    color: #18181B;
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 48.75px;
    font-weight: 600;
    line-height: 140%;

    &-grey {
      color: #C5C5C5;
    }
  }
}

.accordion {
  width: 100%;

  &__item {
    border-bottom: 1.146px solid #adadad;
    padding: 17px 14px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #323142;
    font-family: var(--font-poppins);
    font-size: 25.368px;
    width: 100%;
    font-weight: 600;
    line-height: 252.982%;
    cursor: pointer;
    transition: color 0.3s ease-in-out;
    background: none;
    border: none;
    padding: 0;
    padding: 1.5px;
    text-align: left;

    @include small-tablet {
      line-height: 170%;
    }

    &::after {
      flex-shrink: 0;
      width: 32px;
      height: 32px;
      background: url('../img/questions/plus.svg') center no-repeat;
      border-radius: 32px;
      background-color: var(--dark-color);
      transition: background-color 0.3s ease-in-out;
      content: "";
    }

    &:focus {
      outline: none;
    }

    @include hover {
      color: #32314280;

      &::after {
        background-color: #141416ab;
      }
    }


  }

  &__body {
    padding: 0.75rem 1rem;
    color: var(--black, #12141D);
    font-family: var(--font-poppins);
    font-size: 18px;
    font-weight: 400;
    line-height: 115%;
    overflow: hidden;
    background: #fff;
  }
}

.accordion__item_show .accordion__header::after {
  flex-shrink: 0;
  width: 32px;
  height: 32px;
  background: url('../img/questions/minus.svg') center no-repeat;
  border-radius: 32px;
  background-color: var(--dark-color);
  content: "";
}

.accordion__item_hidden .accordion__header {
  border-bottom-right-radius: 0.25rem;
  border-bottom-left-radius: 0.25rem;
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.accordion__header.focus-visible {
  outline: 1.5px solid var(--dark-color);
}
