.stats {
  padding-bottom: 200px;

  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 46px;
    color: var(--dark-color);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 600;
    line-height: 66.676%;
    text-align: center;

    @include mobile {
      font-size: 40px;
    }

    @include mini-mobile {
      font-size: 30px;
    }
  }
}

.stats-tabs {
  flex-direction: column;
  align-items: center;

  &__panel {
    display: none;
  }

  &__panel--active {
    display: block;
  }

  &__button-more {
    margin-top: 80px;
    align-self: center;
  }

  &__nav {
    justify-content: center;
    margin-bottom: 101px;

    @include mobile {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__nav-item {
    margin-right: 32px;

    &:last-child {
      margin-right: 0;
    }

    &-all {
      @include small-tablet {
        display: none;
      }
    }
  }

  &__nav-btn {
    border-radius: 13.262px;
    border: 2.21px solid var(--dark-color);
    background: none;
    color: var(--greyscale-900, #1A202C);
    font-family: var(--font-manrope);
    font-size: 15.472px;
    font-weight: 600;
    line-height: 150%; /* 23.208px */
    letter-spacing: 0.221px;
    text-transform: none;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    &-all {

      @include hover {
        background-color: var(--dark-color);
      }

      &:active {
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-category {
      background: url('../img/explore/category.svg') left center no-repeat;
      background-size: 25px;
      background-position-x: 16px;
      padding: 17px;
      padding-left: 50px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/category-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
      }

      &:active {
        background: url('../img/explore/category.svg') left center no-repeat;
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-collection {
      background: url('../img/explore/collection.svg') left center no-repeat;
      background-size: 25px;
      background-position-x: 16px;
      padding: 17px;
      padding-left: 50px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/collection-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
      }

      &:active {
        background: url('../img/explore/collection.svg') left center no-repeat;
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-price {
      background: url('../img/explore/price.svg') left center no-repeat;
      background-size: 25px;
      padding-left: 34px;
      background-position-x: 26px;
      padding: 17px 26px;
      padding-left: 60px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/price-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        padding-left: 34px;
        background-position-x: 26px;
        padding: 17px 26px;
        padding-left: 60px;
      }

      &:active {
        background: url('../img/explore/price.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        padding-left: 34px;
        background-position-x: 26px;
        padding: 17px 26px;
        padding-left: 60px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }
  }

  &__nav-btn--active {
    border-color: rgba(28, 29, 32, 0.08);

    color: var(--light-color);
    background-color: var(--accent-color);
    box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
  }

  .table-stats__string:nth-child(n + 9) {
    display: none;
  }
  .table-stats__string.is-visible {
    display: table-row;
  }
}

.table-stats {
  width: 100%;
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 10px 30px;


  th {
    padding-bottom: 8px;
    text-align: start;
  }

  td {
    border-bottom: 1.69px solid #ebe9e9;
    padding-bottom: 24px;
    padding-top: 40px;
  }


  &__header {
    margin: 0;

    .table-collection__string-header {
      color: #93989A;
      font-family: var(--font-poppins);
      font-size: 22px;
      font-weight: 500;
      line-height: 130%; /* 28.6px */
      letter-spacing: 0.22px;

      @include tablet {
        font-size: 15.646px;
        letter-spacing: 0.156px;
      }
    }

    &-volume {

    }

    &-price {
      padding-left: 10px;
      @include small-tablet {
        display: none;
      }
    }

    &-owners {
      padding-left: 10px;
      @include small-tablet {
        display: none;
      }
    }

    &-items {
      @include small-tablet {
        display: none;
      }
    }

    &-24h {
      @include mobile {
        display: none;
      }
    }
  }

  &__string {
    font-family: var(--font-poppins);
  }

  &__name {

  }

  &__image {
    margin-right: 25px;

    @include tablet {
      margin-right: 17px;
    }

    img {
      border-radius: 1750.911px;
      max-width: 80px;
      height: 80px;

      @include tablet {
        max-width: 60px;
        height: 60px;
      }
    }
  }

  &__label {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 28.015px;
    font-style: normal;
    font-weight: 500;
    line-height: 140%;

    @include tablet {
      font-size: 19.923px;
    }
  }

  &__author {
    color: #64748B;
    font-family: var(--font-poppins);
    font-size: 22.762px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;

    @include tablet {
      font-size: 16.188px;
    }
  }

  &__volume {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    padding-left: 34.5px;
    font-size: 28.015px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
    background-size: 31px;

    @include tablet {
      font-size: 17.433px;
      padding-left: 24.5px;
      background-size: 21px;
    }

    @include mobile {
      margin-bottom: 8px;
    }
  }

  &__24h {
    color: #10C352;
    font-family: var(--font-poppins);
    font-size: 24.513px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 31.867px */
    letter-spacing: 0.245px;

    @include tablet {
      font-size: 17.433px;
    }

    @include mobile {
      display: none;
    }

    &-volume {
      color: #10C352;
      font-family: var(--font-poppins);
      font-size: 24.513px;
      font-style: normal;
      font-weight: 500;
      line-height: 130%; /* 31.867px */
      letter-spacing: 0.245px;
      display: none;

      @include tablet {
        font-size: 17.433px;
      }

      @include mobile {
        display: block;
      }
    }
  }

  &__price {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 24.513px;
    padding-left: 34.5px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    background: url('../img/weekly-top/ethereum.svg') left 65px no-repeat;
    background-size: 31px;

    @include tablet {
      font-size: 17.433px;
      padding-left: 24.5px;
      background-size: 21px;
      background-position-y: 61px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__owners {
    color: #010101;
    font-family: var(--font-poppins);
    font-size: 23.654px;
    font-weight: 500;
    line-height: 130%; /* 30.75px */
    letter-spacing: 0.237px;

    @include tablet {
      font-size: 16.822px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__items {
    color: var(--black-01, #010101);
    font-family: var(--font-poppins);
    font-size: 23.654px;
    font-weight: 500;
    line-height: 130%; /* 30.75px */
    letter-spacing: 0.237px;

    @include tablet {
      font-size: 16.822px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__button-more {
    padding-top: 50px;
    width: 152px;
    margin: 0px auto;
  }
}

.tabs__nav-btn-category.focus-visible {
  background: url('../img/explore/category-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  background-position-x: 16px;
  padding: 17px;
  padding-left: 50px;
  color: var(--light-color);
}
.tabs__nav-btn-collection.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
  background: url('../img/explore/collection-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  background-position-x: 16px;
  padding: 17px;
  padding-left: 50px;
}
.tabs__nav-btn-price.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
  background: url('../img/explore/price-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  padding-left: 34px;
  background-position-x: 26px;
  padding: 17px 26px;
  padding-left: 60px;
}
