.inner-collector {
  padding-bottom: 208px;

  @include tablet {
    padding-bottom: 150px;
  }

  @include small-tablet {
    padding-bottom: 106px;
  }

  &__container {
    justify-content: space-between;

    @include small-tablet {
      align-items: center;
      justify-content: center;
      flex-direction: column;
    }
  }

  &__left {
    flex: 0 1 40%;

    @include small-tablet {
      order: 2;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 45px;
    color: #18181B;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 600;
    line-height: 54.4px;
    text-transform: capitalize;

    &-grey {
      color: #C5C5C5;
    }

    @include tablet {
      font-size: 32.003px;
      line-height: 120.889%;
      margin-bottom: 32px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__button {
    text-transform: none;
    padding: 18px 14px 19px 21px;
    span {
      margin-right: 11px;
    }

    @include tablet {
      padding: 14px 14px 14px 15px;

      span {
        font-size: 10.497px;
        line-height: 125%;
      }

      svg {
        width: 15.746px;
        height: 15.746px;
      }
    }
  }

  &__advantages-list {
    margin-bottom: 70px;

    @include tablet {
      margin-bottom: 50px;
    }

    @include small-tablet {
      display: none;
    }
  }

  &__item {
    color: var(--black, #12141D);
    font-family: var(--font-poppins);
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 200%;
    margin-bottom: 12px;
    padding-left: 26px;
    background: url('../img/inner-collector/icon.svg') left center no-repeat;
    background-size: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @include tablet {
      font-size: 11.379px;
      line-height: 200%;
      background-size: 14px;
    }
  }

  &__right {
    position: relative;
    flex: 0 1 60%;

    @include small-tablet {
      flex: 1 1 100%;
      order: 1;
      margin-bottom: 70px;
    }
  }

  &__picture {
  }

  &__dots {

    img {
      position: absolute;
      top: 370px;
      left: 437px;
      width: 400px;
      height: 173px;
      object-fit: cover;
      z-index: -2;

      @include tablet {
        display: none;
      }
    }
  }
}

