html {
  box-sizing: border-box;
  background-color: #f9f9f9;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку

  &--left {
    flex: 0 1 55%;
    padding-right: 48px;
    display: flex;
    flex-direction: column;

    @include small-tablet {
      flex: 1 1 100%;
      width: 100%;
    }
  }

  &--right {
    flex: 0 1 45%;

    @include small-tablet {
      display: none;
    }
  }
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  column-gap: 40px;

  @include tablet {
    grid-template-columns: repeat(8, 1fr);
  }

  @include small-tablet {
    grid-template-columns: repeat(4, 1fr);
  }

  @include mini-mobile {
    grid-template-columns: repeat(8, 1fr); // нужно найти другое решение
  }
}

.flex {
  display: flex;
}

.burger {
  @include burger;
  --burger-height: 20px;
  display: none;
  color: var(--dark-color);

  @include small-tablet {
    display: block;
  }

  &:focus {
    outline: none;
  }

  @include hover {
    color: #1414166b;
  }

  &:active {
    color: #1414162d;
  }
}

.burger.focus-visible {
  color: #1414162d;
}
