.creators {
  padding-bottom: 150px;

  &__container {
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 130px;
    text-align: center;
    color: var(--dark-color);
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 600;
    line-height: 66.676%;

    @include mobile {
      line-height: normal;
      font-size: 35px;
    }

    @include mini-mobile {
      font-size: 30px;
    }
  }

  &__list {
    column-gap: 56px;
    row-gap: 84px;
    margin-bottom: var(--offset);

    @include mini-mobile {
      grid-template-columns: repeat(4, 1fr);
      column-gap: 42px;
    }
  }

  &__item:nth-child(n + 7) {
    display: none;
  }
  &__item.is-visible {
    display: block;
  }

  &__btn {
    background-color: #fff;
    color: #000;

      &:focus {
        outline: none;
      }

      &:active {
        background-color: var(--dark-color);
        color: var(--light-color);
      }
  }
}

.item-creators {
  grid-column: 4 span;
  border-radius: 25px;
  background: #F2F2F2;
  box-shadow: 30px 15px 80px 0px rgba(191, 191, 191, 0.60);

  &__top {
    img {
      width: 100%;
      border-top-left-radius: 25px;
      border-top-right-radius: 25px;
    }
  }

  &__main {
    flex-direction: column;
    align-items: center;
  }

  &__avatar {
    margin-top: -65px;
    margin-bottom: 17px;
  }

  &__image {
    width: 110px;
    height: 110px;
  }

  &__nick {
    align-items: center;
  }

  &__name {
    margin: 0;
    color: var(--light-100, #000);
    font-family: var(--font-poppins);
    font-size: 23.724px;
    font-weight: 600;
    line-height: 141.667%;
  }

  &__confirmed {
    margin-left: 10px;
    width: 23.499px;
    height: 23.499px;
  }

  &__bottom {
    padding: 0 27px;
    width: 100%;
  }

  &__text {
    justify-content: space-between;
  }

  &__numbers {
    margin-bottom: 33px;
  }

  &__label {
    color: var(--dark-color);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 17.793px;
    font-weight: 500;
    line-height: 144.444%;

    &-eth {
      padding-left: 17px;
      background: url('../img/weekly-top/ethereum.svg') left center no-repeat;
      background-size: 17px;

      &-big {
        padding-left: 0px;
      }
    }
  }

  &__descr {
    margin: 0;
    color: var(--gray-3, #828282);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 12.85px;
    font-weight: 400;
    line-height: 123.077%;
    text-transform: capitalize;
  }

  &__list {
    justify-content: space-between;
  }

  &__item {
    margin-right: 10px;
    margin-bottom: 37px;
    position: relative;
  }

  &__images {
    border-radius: 9.931px;
    flex-shrink: 0;


    img {
      flex-shrink: 0;
    }
  }

  &__count {
    position: absolute;
    top: 0;
    left: 0;
    padding-top: 40%;
    height: 95%;
    border-radius: 9.931px;
    width: 100%;
    text-align: center;
    color: var(--light-color);
    font-family: 'Urbanist', sans-serif;
    font-size: 19.862px;
    font-weight: 700;
    line-height: 21.849px; /* 110% */
    letter-spacing: 0.199px;
    background: rgba(16, 16, 16, 0.50);
  }
}

.creators__btn.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
}
