.header {
  padding-top: 46px;
  padding-bottom: 99px;

  @include tablet {
    padding-top: 32px;
    padding-bottom: 70px;
  }

  @include mobile {
    padding-top: 30px;
    padding-bottom: 0;
    margin-bottom: 75px;
  }

  &__wrapper {
    align-items: center;

    @include tablet {
      justify-content: space-between;
    }

    @include mobile {
      padding-bottom: 41px;
      border-bottom: 0.502px solid #cbcbcb;
    }

    &--connect {
      @include mobile {
        border: none;
        padding-bottom: 100px;
        margin-bottom: 0;
      }
    }
  }

  .logo {
    min-height: 53px;
    min-width: 53px;
    margin-right: 57px;

    @include tablet {
      min-width: 38px;
      min-height: 38px;
      margin-right: 40px;
    }
  }

  .nav {
    width: 100%;
    align-items: center;
    justify-content: space-between;

    &--connect {
      display: none;
    }

    @include small-tablet {
      position: fixed;
      left: 0;
      top: 0;
      width: 80%;
      height: 100vh;
      padding: 200px 0;
      background-color: #141414;
      z-index: 100;
      display: flex;
      flex-direction: column;
      overflow-y: auto;
      transform: translateX(-100%);
      visibility: hidden;
      transition: transform 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }

    &__list {
      align-items: center;

      @include small-tablet {
        flex-direction: column;
        justify-self: center;
        order: 2;
      }
    }

    &__item {
      margin-right: 53.49px;

      @include tablet {
        margin-right: 38px;
      }

      @include small-tablet {
        margin-right: 0;
        margin-bottom: 25px;
      }

      &:first-child {
        @include small-tablet {
          padding-top: 50px;
        }
      }

      &:last-child {
        margin-right: 117.49px;

        @include tablet {
          margin-right: 40px;
        }

        @include small-tablet {
          margin-right: 0;
          margin-bottom: 0;
          padding-bottom: 50px;
        }
      }
    }

    &__link {
      color: #606060;
      font-family: var(--font-inter);
      font-size: 17.829px;
      font-style: normal;
      font-weight: 500;
      line-height: 150%;
      text-transform: uppercase;
      transition: color 0.3s ease-in-out;

      @include hover {
        color: var(--accent-color);
      }

      &:active {
        color: var(--active-color);

        @include small-tablet {
          color: #7c7c8346;
        }
      }

      @include tablet {
        font-size: 12.679px;
      }

      @include small-tablet {
        font-size: 20px;
      }

      &-active {
        color: var(--dark-color);
        @include small-tablet {
          color: #cecece;
        }
      }
    }
  }

  &__form {
    margin-right: 30px;

    @include small-tablet {
      margin-right: 0;
      order: 1;
    }
  }

  &__button {
    @include tablet {
      padding: 11.5px 17.25px;
      font-size: 11.501px;
    }
    @include small-tablet {
      order: 3;
      background-color: none;
      color: var(--light-color);
      border-color: var(--light-color);
    }
  }
}

.nav__link.focus-visible {
  outline: 2px solid var(--active-color);
  border-radius: 10%;
}

.nav.menu--active {
  visibility: visible;
  transform: none;
}
