.aside {
}

.swiper-connect {
  flex-direction: column;
  align-items: center;
  background-color: var(--dark-color);
  height: 100vh;
  position: relative;

  &__wrapper {
    position: absolute;
    top: 0;
    right: 0;
  }


  &__slide {
    flex-direction: column;
    align-items: center;
  }

  &__text {
    padding: 0 141px;

    @include small-notebook {
      padding: 0 20px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 14.03px;
    color: #FFF;
    text-align: center;
    font-family: var(--font-dm);
    font-size: 47.339px;
    font-weight: 700;
    line-height: 56.807px; /* 120% */
    letter-spacing: -0.947px;
  }

  &__descr {
    color: #6D6D6D;
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 16.569px;
    font-weight: 400;
    line-height: 171.429%;
  }

  &__buttons {
    position: absolute;
    bottom: 10%;
    z-index: 21;
    width: 183px;
    min-height: 69px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--neutrals-8, #FCFCFD);
    box-shadow: 0px 43.11688232421875px 34.493507385253906px -25.87013053894043px rgba(15, 15, 15, 0.12);
    border-radius: 17.247px;

    @include mobile {
      display: none;
    }
}
  &__nav-item {
    flex: 0 0 50%;
    max-width:50%;
    text-align: center;
    height: 30px;
    line-height: 30px;
    cursor: pointer;
    background: none;
    border: none;

    &:first-child {
      border-right: 2.156px solid var(--neutrals-6, #E6E8EC);
    }

    &:not(.swiper-button-disabled):hover{
      opacity:.8;
    }
  }
}
