.create {
  padding-bottom: 160px;

  @include small-tablet {
    margin-bottom: 250px;
    padding-bottom: 0;
  }

  @include mobile {
    margin-bottom: 190px;
  }

  &__container {
    border-radius: 30px;
    background: var(--dark-color);
    justify-content: space-between;
    padding: 0 31px 0 66px;

    @include small-tablet {
      position: relative;
      flex-direction: column;
      align-items: center;
      padding: 0 31px 82px 66px;
    }

    @include small-tablet {
      justify-content: start;
    }
  }

  &__left {
    padding: 44px 0 51px 0;

    @include tablet {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 31px 0 36px 0
    }

    @include small-tablet {
      align-items: center;
      justify-content: start;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 12px;
    color: var(--light-color);
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 600;
    line-height: 130%; /* 58.5px */
    letter-spacing: 0.45px;

    @include tablet {
      font-size: 32.003px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 51px;
    color: #D4D4D4;
    font-family: var(--font-poppins);
    font-size: 20.265px;
    font-style: normal;
    font-weight: 500;
    line-height: 130%; /* 26.345px */
    letter-spacing: 0.203px;

    @include tablet {
      font-size: 14.412px;
    }

    @include small-tablet {
      margin-bottom: 15px;
    }
  }

  &__buttons {
    column-gap: 30px;
  }

  &__button {
    color: var(--dark-color);
    font-family: var(--font-outfit);
    font-size: 21.357px;
    font-weight: 600;
    line-height: 130%; /* 27.764px */
    letter-spacing: 0.214px;
    border-radius: 12.204px;
    background-color: var(--light-color);
    text-transform: none;
    padding: 20px 26px;

    @include tablet {
      padding: 14px 18px;
      font-size: 15.204px;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      border-color: rgba(28, 29, 32, 0.08);
      color: var(--light-color);
      background-color: #ffffffb6;
      box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
    }

    &:active {
      color: var(--light-color);
      background-color: var(--active-color);
      border-color: var(--light-color);
    }

    &-black {
      padding: 20px 30px;
      border: 2.288px solid var(--light-color);
      color: var(--light-color);
      background: none;

      &:active {
        color: var(--dark-color);
        background-color: var(--light-color);
        border-color: var(--light-color);
      }
    }
  }


  &__image {
    padding: 31px 0;
    position: relative;

    @include small-tablet {
      position: absolute;
      top: 67%;
      left: auto;
    }

    @include mobile {
      top: 65%;
    }
  }

  &__blur {
    z-index: 1;
    max-width: 369px;
    max-height: 249px;
    filter: blur(26.437305450439453px);

    @include small-tablet {
      filter: blur(15.404114723205566px);
    }

    @include mobile {
      img {
        max-width: 262.426px;
        max-height: 177.084px;
        border-radius: 9.202px;
      }
    }
  }

  &__picture {
    position: absolute;
    top: 31px;
    left: 0;

    @include mobile {

      img {
        max-width: 262.426px;
        max-height: 177.084px;
        border-radius: 9.202px;
      }
    }
  }
}

.create__button.focus-visible {
  border-color: rgba(28, 29, 32, 0.08);
  color: var(--light-color);
  background-color: #ffffffb6;
  box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
}
