.discover {
  padding-bottom: 143px;

  @include notebook {
    padding-bottom: 50px;
  }

  @include tablet {
    padding-bottom: 60px;
  }


  @include small-tablet {
    padding-bottom: 0;
  }

  &__container {
    @include notebook {
      flex-direction: column;
    }
  }
  &__left {
    padding-top: 21px;
    flex: 0 1 40.5%;
    flex-direction: column;

    @include notebook {
      margin-bottom: 50px;
      align-items: center;
    }
  }

  &__title {
    padding-top: 7px;
    margin: 0;
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 75px;
    font-weight: 600;
    line-height: 120%;
    letter-spacing: -3px;
    text-transform: capitalize;
    margin-bottom: 16px;

    @include tablet {
      font-size: 53.339px;
    }

    @include small-tablet {
      text-align: center;
      font-size: 47.399px;
      font-weight: 600;
      line-height: 120%; /* 56.879px */
      letter-spacing: -1.896px;
      text-transform: capitalize;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 45px;
    color: var(--text-second-color);
    text-align: justify;
    font-family: var(--font-inter);
    font-size: 18px;
    font-weight: 400;
    line-height: 155.556%;

    @include tablet {
      font-size: 12.801px;
      margin-bottom: 20px;
    }

    @include small-tablet {
      text-align: center;
      font-size: 11.376px;
      font-weight: 400;
      line-height: 17.696px;
    }

    &-link {
      color: var(--dark-color);
    }
  }

  &__buttons {
    margin-bottom: 82px;

    @include tablet {
      margin-bottom: 45px;
    }

    @include small-tablet {
      margin-bottom: 35px;
    }
  }

  &__button {

    &-explore {
      padding: 18px 30px;
      margin-right: 23px;

      @include tablet {
        padding: 12.8px 20.6px;
        font-size: 11.571px;
      }
    }

    &-create {
      padding: 18px 21px;
      border: 1.5px solid #000;
      background-color: #fff;
      color: #000;

      @include tablet {
        padding: 12.8px 15px;
        font-size: 11.571px;
      }

      &:focus {
        outline: none;
      }

      &:active {
        background-color: var(--dark-color);
        color: var(--light-color);
      }
    }
  }

  &__right {
    position: relative;
    flex: 0 1 50%;
    min-height: 100%;
  }

  &__swiper {
    position: absolute;
    z-index: 2;
    top: -43px;
    left: 32px;
    max-width: 900px;
    min-height: 690px;

    @include notebook {
      position: initial;
      max-height: 690px;
      min-height: 690px;
    }

    @include small-tablet {
      min-height: 35%;
      padding-bottom: 50px;
    }

    @include mobile {
      max-width: 100%;
    }

    &-buttons {
      position: relative;
      z-index: 21;
      max-width: 183px;
      min-height: 69px;
      margin-left: 191px;
      margin-top: -130px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--neutrals-8, #FCFCFD);
      box-shadow: 0px 43.11688232421875px 34.493507385253906px -25.87013053894043px rgba(15, 15, 15, 0.12);
      border-radius: 17.247px;

      @include small-tablet {
        margin-left: 115px;
        margin-top: -90px;
        max-width: 143px;
        min-height: 50px;
      }

      @include mobile {
        display: none;
      }
}
    &-nav-item {
      flex: 0 0 50%;
      max-width:50%;
      text-align: center;
      height: 30px;
      line-height: 30px;
      cursor: pointer;
      background: none;
      border: none;

      &:first-child {
        border-right: 2.156px solid var(--neutrals-6, #E6E8EC);
      }

      &:not(.swiper-button-disabled):hover{
        opacity:.8;
      }
    }
  }
}

.numbers {

  &__item {
    margin-right: 65px;
    display: flex;
    flex-direction: column;

    @include mobile {
      margin-right: 30px;
    }



    &:last-child {
      margin-right: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    color: #141416;
    font-family: var(--font-sans);
    font-size: 36.771px;
    font-weight: 600;
    line-height: 125%;

    @include tablet {
      font-size: 26.151px;
    }
  }

  &__descr {
    margin: 0;
    color: #848586;
    font-family: var(--font-inter);
    font-size: 12.257px;
    font-weight: 500;
    line-height: 150%;

    @include tablet {
      font-size: 8.717px;
    }
  }
}

.discover__swiper-nav-item.focus-visible {
  outline: 1.5px solid var(--dark-color);
}

.discover__button-create.focus-visible {
  border-color: rgba(28, 29, 32, 0.08);
  color: var(--light-color);
  fill: var(--light-color);
  stroke: var(--light-color);
  background-color: var(--accent-color);
  box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
}
