/* stylelint-disable */

@mixin big-desktop {
  @media (max-width: (1440px)) {
    @content;
  }
}

@mixin notebook {
  @media (max-width: (1273px)) {
    @content;
  }
}

@mixin small-notebook {
  @media (max-width: (1163px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin medium-tablet {
  @media (max-width: (918px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (786px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (586px)) {
    @content;
  }
}

@mixin mini-mobile {
  @media (max-width: (425px)) {
    @content;
  }
}
