.weekly-top {
  background-color: #F1F1F1;
  flex-direction: column;
  align-items: center;

  &__title {
    padding-top: 70px;
    margin: 0;
    margin-bottom: 100px;
    color: #C5C5C5;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 600;
    line-height: 59.812%;

    @include tablet {
      font-size: 32px;
    }
  }

  &__swiper {
    position: relative;
    margin: 0;
    width: 100%;
    margin: 0 auto;
    flex-direction: column;

    &-wrapper {
      margin-bottom: 114px;
    }

    &-buttons {
      align-self: center;
      min-width: 183px;
      min-height: 69px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: var(--neutrals-8, #FCFCFD);
      box-shadow: 0px 43.11688232421875px 34.493507385253906px -25.87013053894043px rgba(15, 15, 15, 0.12);
      border-radius: 17.247px;
      margin-bottom: 100px;
    }

    &-nav-item {
      flex: 0 0 50%;
      max-width:50%;
      text-align: center;
      height: 30px;
      line-height: 30px;
      cursor: pointer;
      background: none;
      border: none;

      &:focus {
        outline: none;
      }

      &:first-child {
        border-right: 2.156px solid var(--neutrals-6, #E6E8EC);
      }

      &:not(.swiper-button-disabled):hover{
        opacity:.8;
      }
    }
  }

  &__slide {
    max-width: 253.126px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 14px 14px 20px;
    background-color: var(--light-color);
    border-radius: 23.479px;
  }
}

.slide {

  &__top {
    position: relative;
    margin-bottom: 15px;
  }

  &__timer {
    position: absolute;
    right: 14px;
    top: 11px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 8px 14px;
    border: 1.176px solid rgba(28, 29, 32, 0.08);
    border-radius: 9.41px;
    background-color: rgba(28, 29, 32, 0.35);
    box-shadow: 0px 4.705041408538818px 14.115126609802246px 0px rgba(28, 29, 32, 0.08);
    backdrop-filter: blur(2.352520704269409px);
    color: var(--light-color, #FFF);
    font-family: var(--font-poppins);
    font-size: 14.115px;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: 0.118px;
    margin-bottom: 19px;
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 21px;
    font-weight: 600;
    line-height: 140%;
  }

  &__bottom {
    display: flex;
  }

  &__left {
    flex: 0 1 50%;
  }

  &__bid {
    color: #94A3B8;
    font-family: var(--font-poppins);
    font-size: 14.115px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.118px;
    margin-bottom: 5px;
  }

  &__amount {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 16.468px;
    font-weight: 400;
    line-height: 150%;
    padding-left: 20px;
    background: url('../img/weekly-top/ethereum.svg') 0 0 no-repeat;
    background-size: 21px;
    background-position-x: -5px;
  }

  &__button {
    flex: 0 1 50%;
    padding: 19px 9px;
  }
}

.weekly-top__swiper-nav-item.focus-visible {
  outline: 1.5px solid var(--dark-color);
}
