.explore {
  margin-bottom: 75px;

  &__container {
    flex-direction: column;
    align-items: center;
    padding-bottom: 103px;
    border-bottom : 0.711px solid #cbcbcb;
  }

  &__container-more {
    border: none;
  }

  &__title {
    margin: 0;
    margin-bottom: 66px;
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 45px;
    font-style: normal;
    font-weight: 600;
    line-height: 59.812%;
    text-align: center;

    @include mobile {
      font-size: 35px;
      line-height: 125%;
    }
  }

  &__button {
    margin-top: 103px;
  }
}

.tabs {

  &__button-more {
    margin-top: 80px;
    align-self: center;
  }

  &__nav {
    justify-content: center;
    margin-bottom: 101px;

    @include mobile {
      flex-wrap: wrap;
      gap: 20px;
    }
  }

  &__nav-item {
    margin-right: 22px;

    &:last-child {
      margin-right: 0;
    }

    &-all {
      @include small-tablet {
        display: none;
      }
    }
  }

  &__nav-btn {
    border-radius: 13.262px;
    border: 2.21px solid var(--dark-color);
    background: none;
    color: var(--greyscale-900, #1A202C);
    font-family: var(--font-manrope);
    font-size: 15.472px;
    font-weight: 600;
    line-height: 150%; /* 23.208px */
    letter-spacing: 0.221px;
    text-transform: none;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    &-all {

      @include hover {
        background-color: var(--dark-color);
      }

      &:active {
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-category {
      background: url('../img/explore/category.svg') left center no-repeat;
      background-size: 25px;
      background-position-x: 16px;
      padding: 17px;
      padding-left: 50px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/category-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
      }

      &:active {
        background: url('../img/explore/category.svg') left center no-repeat;
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-collection {
      background: url('../img/explore/collection.svg') left center no-repeat;
      background-size: 25px;
      background-position-x: 16px;
      padding: 17px;
      padding-left: 50px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/collection-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
      }

      &:active {
        background: url('../img/explore/collection.svg') left center no-repeat;
        background-size: 25px;
        background-position-x: 16px;
        padding: 17px;
        padding-left: 50px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }

    &-price {
      background: url('../img/explore/price.svg') left center no-repeat;
      background-size: 25px;
      padding-left: 34px;
      background-position-x: 26px;
      padding: 17px 26px;
      padding-left: 60px;
      transition: background 0.3s ease-in-out;

      @include hover {
        background: url('../img/explore/price-hover.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        padding-left: 34px;
        background-position-x: 26px;
        padding: 17px 26px;
        padding-left: 60px;
      }

      &:active {
        background: url('../img/explore/price.svg') left center no-repeat;
        background-color: var(--dark-color);
        background-size: 25px;
        padding-left: 34px;
        background-position-x: 26px;
        padding: 17px 26px;
        padding-left: 60px;
        border-color: rgba(28, 29, 32, 0.08);
        color: var(--light-color);
        background-color: var(--accent-color);
        box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
      }
    }
  }

  &__nav-btn--active {
    border-color: rgba(28, 29, 32, 0.08);

    color: var(--light-color);
    background-color: var(--accent-color);
    box-shadow: 0px 4.72005033493042px 14.16015338897705px 0px rgba(28, 29, 32, 0.08);
  }


  &__panel {
    transition: opacity 5s ease-in-out;
    display: none;
    opacity: 0;
  }

  &__panel--active {
    display: flex;
    flex-direction: column;
    align-items: center;
    opacity: 100%;
    transition: opacity 5s ease-in-out;
  }

  &__list {
    column-gap: 40px;
    row-gap: 67px;
  }

  &__item {
    grid-column: 3 span;

    @include tablet {
      grid-column: 2 span;
    }

    @include medium-tablet {
      grid-column: 4 span;
    }

    @include small-tablet {
      grid-column: 2 span;
    }

    @include mobile {
      grid-column: 2 / 4;
    }

    @include mini-mobile {
      grid-column: 2 / 8;
    }

    &:nth-child(n + 13) {
    display: none;
    }

    &.is-visible {
      display: block;
    }
  }

  &__card {
    display: block;
    color: inherit;
    flex-direction: column;
    padding: 14px 14px 20px;
    background-color: var(--light-color);
    border-radius: 23.479px;

    @include mini-mobile {

    }
  }

  &__top {
    position: relative;
    margin-bottom: 15px;
  }

  &__image {

    img {
      border-radius: 23px;
    }
  }

  &__timer {
    position: absolute;
    right: 14px;
    top: 11px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 8px 14px;
    border: 1.176px solid rgba(28, 29, 32, 0.08);
    border-radius: 9.41px;
    background-color: rgba(28, 29, 32, 0.35);
    box-shadow: 0px 4.705041408538818px 14.115126609802246px 0px rgba(28, 29, 32, 0.08);
    backdrop-filter: blur(2.352520704269409px);
    color: var(--light-color, #FFF);
    font-family: var(--font-poppins);
    font-size: 14.115px;
    font-weight: 500;
    line-height: 150%;
    letter-spacing: 0.118px;
    margin-bottom: 19px;
  }

  &__title {
    margin: 0;
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 21px;
    font-weight: 600;
    line-height: 140%;
  }

  &__bottom {
    display: flex;
    align-items: center;

    @include notebook {
      align-items: initial;
      flex-direction: column;
    }

    @include tablet {
      align-items: center;
      flex-direction: row;
    }

    @include mobile {
      align-items: initial;
      flex-direction: column;
    }
  }

  &__left {
    flex: 0 1 50%;

    @include notebook {
      margin-bottom: 20px;
    }

    @include tablet {
      margin-bottom: 0;
    }

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__bid {
    color: #94A3B8;
    font-family: var(--font-poppins);
    font-size: 14.115px;
    font-weight: 400;
    line-height: 150%;
    letter-spacing: 0.118px;
    margin-bottom: 5px;
  }

  &__amount {
    color: var(--dark-color);
    font-family: var(--font-poppins);
    font-size: 16.468px;
    font-weight: 400;
    line-height: 150%;
    padding-left: 20px;
    background: url('../img/weekly-top/ethereum.svg') 0 0 no-repeat;
    background-size: 21px;
    background-position-x: -5px;
  }

  &__button {
    text-align: center;
    flex: 0 1 50%;
    padding: 15px 25px;
    color: var(--light-color);
    font-family: var(--font-poppins);
    font-size: 14.16px;
    font-weight: 600;
    line-height: 150%; /* 21.24px */
    letter-spacing: 0.118px;
  }

  &__btn {
    cursor: pointer;
  }
}

.tabs__nav-btn-all.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
}
.tabs__nav-btn-category.focus-visible {
  background: url('../img/explore/category-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  background-position-x: 16px;
  padding: 17px;
  padding-left: 50px;
  color: var(--light-color);
}
.tabs__nav-btn-collection.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
  background: url('../img/explore/collection-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  background-position-x: 16px;
  padding: 17px;
  padding-left: 50px;
}
.tabs__nav-btn-price.focus-visible {
  background-color: var(--dark-color);
  color: var(--light-color);
  background: url('../img/explore/price-hover.svg') left center no-repeat;
  background-color: var(--dark-color);
  background-size: 25px;
  padding-left: 34px;
  background-position-x: 26px;
  padding: 17px 26px;
  padding-left: 60px;
}
