.footer {
  background-color: var(--dark-color);

  &__container {
    padding: 92px 0 81px 0;

    @include notebook {
      padding: 92px 30px 81px 30px;
    }

    @include tablet {
      padding: 92px 30px 36px 30px;
    }
  }

  &__top-main {
    justify-content: space-between;
    align-items: center;

    @include tablet {
      margin-bottom: 50px;
    }

    @include mini-mobile {
      flex-direction: column;
      justify-content: start;
    }
  }

  &__top {
    padding-bottom: 25px;
    margin-bottom: 57px;
    border-bottom: 1px solid rgba(129, 129, 129, 0.25);

    @include tablet {
      padding-bottom: 25px;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;

    span {
      margin-left: 10px;
      color: var(--light-color);
      font-family: var(--font-poppins);
      font-size: 29.922px;
      font-style: normal;
      font-weight: 600;
      line-height: 130%;
    }

    @include mini-mobile {
      margin-bottom: 20px;
    }
  }

  &__list {

    @include tablet {
      display: none;
    }

    &-second {
      display: none;

      @include tablet {
        display: flex;
        flex-direction: column;
      }
    }
  }

  &__item {
    margin-right: 70px;

    &:last-child {
      margin: 0;
    }

    @include tablet {
      margin-bottom: 30px;
      margin-right: 0;
    }
  }

  &__link {
    color: #B9B9B9;
    font-family: var(--font-inter);
    font-size: 17.829px;
    font-weight: 500;
    line-height: 150%;
    text-transform: capitalize;
    transition: color 0.3s ease-in-out;

    &:focus {
      outline: none;
    }

    @include hover {
      color: #b9b9b9a8;
    }

    &:active {
      color: #b9b9b942;
    }
  }

  &__bottom {
    justify-content: space-between;
    align-items: center;

    @include tablet {
      justify-content: center;
    }
  }

  &__copy {
    color: #999;
    font-family: var(--font-inter);
    font-size: 17px;
    font-weight: 500;
    line-height: 147.059%;
  }

  &__list-social {

    @include tablet {
      display: none;
    }

    &-first {
      display: none;

      @include tablet {
        display: flex;
      }
    }
  }

  &__item-social {
    margin-right: 54px;

    &:last-child {
      margin: 0;
    }

    @include mobile {
      margin-right: 20px;
    }
  }

  &__link-social {
    svg {
      fill: #535353;
      transition: fill 0.3s ease-in-out;
    }

    &:focus {
      outline: none;
    }

    @include hover {
      svg {
        fill: var(--light-color);
      }
    }

    &:active {
      svg {
        fill: #9b9b9b;
      }
    }
  }
}

.footer__link.focus-visible {
  outline: 1.5px solid var(--light-color);
}

.footer__link-social.focus-visible {
  svg {
    fill: var(--light-color);
  }
}
