.community {
  padding-bottom: 219px;

  @include tablet {
    padding-bottom: 152px;
  }

  @include small-tablet {
    padding-bottom: 112px;
  }

  &__container {
    max-width: 1061px;
    min-height: 1085px;
    background: url("../img/community/cover.png") no-repeat;
    background-position: center;
    background-size: contain;
    align-items: center;
    justify-content: center;

    @include small-tablet {
      background-size: cover;
      min-height: 531px;
    }
  }

  &__wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 597px;
    min-height: 800px;
  }

  &__title {
    margin: 0;
    margin-bottom: 31px;
    color: #18181B;
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 45px;
    font-weight: 700;
    line-height: 120.889%;
    text-transform: capitalize;
    text-align: center;

    &-grey {
      color: #C5C5C5;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 49px;
    color: var(--black, #12141D);
    text-align: center;
    font-family: var(--font-poppins);
    font-size: 18px;
    font-weight: 400;
    line-height: 177.778%;
    text-align: center;

    @include tablet {
      margin-bottom: 35px;
      font-size: 19px;
    }

    @include small-tablet {
      font-size: 17px;
      line-height: 173.924%;
      margin-bottom: 23px;
    }
  }

  &__button {
    text-align: center;
    font-size: 14.76px;
    line-height: 125%;
    padding: 18px 22px;
    text-transform: none;
    font-weight: 500;

    @include tablet {
      font-size: 18.497px;
      padding: 12px 15px;
      font-weight: 400;
    }

    @include small-tablet {
      font-size: 15px;
      line-height: 125%;
      padding: 11px 13px;
      font-weight: 500;
    }
  }
}
